// stylelint-disable value-no-vendor-prefix, selector-max-id<br /><br />#gradient {<br /><br />  // Horizontal gradient, from left to right<br />  //<br />  // Creates two color stops, start and end, by specifying a color and position for each color stop.<br />  // Color stops are not available in IE9 and below.<br />  .horizontal(@start-color: #555; @end-color: #333; @start-percent: 0%; @end-percent: 100%) {<br />    background-image: -webkit-linear-gradient(left, @start-color @start-percent, @end-color @end-percent); // Safari 5.1-6, Chrome 10+<br />    background-image: -o-linear-gradient(left, @start-color @start-percent, @end-color @end-percent); // Opera 12<br />    background-image: linear-gradient(to right, @start-color @start-percent, @end-color @end-percent); // Standard, IE10, Firefox 16+, Opera 12.10+, Safari 7+, Chrome 26+<br />    filter: e(%("progid:DXImageTransform.Microsoft.gradient(startColorstr='%d', endColorstr='%d', GradientType=1)", argb(@start-color), argb(@end-color))); // IE9 and down<br />    background-repeat: repeat-x;<br />  }<br /><br />  // Vertical gradient, from top to bottom<br />  //<br />  // Creates two color stops, start and end, by specifying a color and position for each color stop.<br />  // Color stops are not available in IE9 and below.<br />  .vertical(@start-color: #555; @end-color: #333; @start-percent: 0%; @end-percent: 100%) {<br />    background-image: -webkit-linear-gradient(top, @start-color @start-percent, @end-color @end-percent);  // Safari 5.1-6, Chrome 10+<br />    background-image: -o-linear-gradient(top, @start-color @start-percent, @end-color @end-percent);  // Opera 12<br />    background-image: linear-gradient(to bottom, @start-color @start-percent, @end-color @end-percent); // Standard, IE10, Firefox 16+, Opera 12.10+, Safari 7+, Chrome 26+<br />    filter: e(%("progid:DXImageTransform.Microsoft.gradient(startColorstr='%d', endColorstr='%d', GradientType=0)", argb(@start-color), argb(@end-color))); // IE9 and down<br />    background-repeat: repeat-x;<br />  }<br /><br />  .directional(@start-color: #555; @end-color: #333; @deg: 45deg) {<br />    background-image: -webkit-linear-gradient(@deg, @start-color, @end-color); // Safari 5.1-6, Chrome 10+<br />    background-image: -o-linear-gradient(@deg, @start-color, @end-color); // Opera 12<br />    background-image: linear-gradient(@deg, @start-color, @end-color); // Standard, IE10, Firefox 16+, Opera 12.10+, Safari 7+, Chrome 26+<br />    background-repeat: repeat-x;<br />  }<br />  .horizontal-three-colors(@start-color: #00b3ee; @mid-color: #7a43b6; @color-stop: 50%; @end-color: #c3325f) {<br />    background-image: -webkit-linear-gradient(left, @start-color, @mid-color @color-stop, @end-color);<br />    background-image: -o-linear-gradient(left, @start-color, @mid-color @color-stop, @end-color);<br />    background-image: linear-gradient(to right, @start-color, @mid-color @color-stop, @end-color);<br />    filter: e(%("progid:DXImageTransform.Microsoft.gradient(startColorstr='%d', endColorstr='%d', GradientType=1)", argb(@start-color), argb(@end-color))); // IE9 and down, gets no color-stop at all for proper fallback<br />    background-repeat: no-repeat;<br />  }<br />  .vertical-three-colors(@start-color: #00b3ee; @mid-color: #7a43b6; @color-stop: 50%; @end-color: #c3325f) {<br />    background-image: -webkit-linear-gradient(@start-color, @mid-color @color-stop, @end-color);<br />    background-image: -o-linear-gradient(@start-color, @mid-color @color-stop, @end-color);<br />    background-image: linear-gradient(@start-color, @mid-color @color-stop, @end-color);<br />    filter: e(%("progid:DXImageTransform.Microsoft.gradient(startColorstr='%d', endColorstr='%d', GradientType=0)", argb(@start-color), argb(@end-color))); // IE9 and down, gets no color-stop at all for proper fallback<br />    background-repeat: no-repeat;<br />  }<br />  .radial(@inner-color: #555; @outer-color: #333) {<br />    background-image: -webkit-radial-gradient(circle, @inner-color, @outer-color);<br />    background-image: radial-gradient(circle, @inner-color, @outer-color);<br />    background-repeat: no-repeat;<br />  }<br />  .striped(@color: rgba(255, 255, 255, .15); @angle: 45deg) {<br />    background-image: -webkit-linear-gradient(@angle, @color 25%, transparent 25%, transparent 50%, @color 50%, @color 75%, transparent 75%, transparent);<br />    background-image: -o-linear-gradient(@angle, @color 25%, transparent 25%, transparent 50%, @color 50%, @color 75%, transparent 75%, transparent);<br />    background-image: linear-gradient(@angle, @color 25%, transparent 25%, transparent 50%, @color 50%, @color 75%, transparent 75%, transparent);<br />  }<br />}<br />